  docreateDrug() {
    if (!this.drug["quantity"]) {
      this.$alert("请输入药品数量");
      return;
    }

    let b = this.drug["quantity"] > 0;

    if (!b) {
      this.$alert("请输入正确的药品数量");
      return;
    }

    // if (!this.drug["price"]) {
    //   this.$alert("请输入药品金额");
    //   return;
    // }
    // let a = this.drug["price"] > 0;
    // if (!a) {
